/* Split the screen in half */
.split {
	height: 100%;
	width: 49%;
	position: fixed;
	z-index: 1;
	top: 0;
	overflow-x: hidden;
	padding-top: 20px;
}

/* Control the left side */
.left {
	left: 0;
}

/* Control the right side */
.right {
	right: 0;
}

.textDiv {
	margin-left: 1%;
}

.resultDiv {
	margin-right: 1%;
}

#txtArea {
	border: 1px black solid;
	width: 100%;
	height: 95%;
	padding-left: 8px;
	font-size: 18pt;
	resize: none;
	outline: none;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

#result {
	font-size: 35px;
	text-align: center;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: rgb(46, 102, 206);
	height: 30%;
	border: 1px solid black;
}

#timeList {
	font-size: 30px;
	text-align: left;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	color: rgb(0, 0, 0);
	border: 1px solid red;
	line-height: 55px;
}
